'use client'

import { useState } from 'react'
import { Note, NoteWithReferences } from '@/lib/database.types'
import ReferenceModal from './ReferenceModal'

interface NoteFormProps {
  onSubmit: (noteData: {
    title: string
    content: string
    note_type: Note['note_type']
    tags: string[]
    references?: string[]
  }) => Promise<void>
  onCancel: () => void
  initialData?: Partial<NoteWithReferences>
  isLoading?: boolean
}

const NOTE_TYPES = [
  { value: 'general', label: 'üìù General', description: 'General notes and ideas' },
  { value: 'npc', label: 'üßô‚Äç‚ôÇÔ∏è NPC', description: 'Non-player characters' },
  { value: 'location', label: 'üè∞ Location', description: 'Places and areas' },
  { value: 'quest', label: '‚öîÔ∏è Quest', description: 'Adventures and missions' },
  { value: 'session', label: 'üé≤ Session', description: 'Game session notes' },
  { value: 'item', label: '‚ö° Item', description: 'Magical items and equipment' },
  { value: 'lore', label: 'üìö Lore', description: 'World building and history' }
] as const

const NOTE_TYPE_ICONS = {
  general: 'üìù',
  npc: 'üßô‚Äç‚ôÇÔ∏è',
  location: 'üè∞',
  quest: '‚öîÔ∏è',
  session: 'üé≤',
  item: '‚ö°',
  lore: 'üìö'
}

export default function NoteForm({ onSubmit, onCancel, initialData, isLoading }: NoteFormProps) {
  const [title, setTitle] = useState(initialData?.title || '')
  const [content, setContent] = useState(initialData?.content || '')
  const [noteType, setNoteType] = useState<Note['note_type']>(initialData?.note_type || 'general')
  const [tagsInput, setTagsInput] = useState(initialData?.tags?.join(', ') || '')
  const [showReferenceModal, setShowReferenceModal] = useState(false)
  
  // Track references for new notes or editing
  const [pendingReferences, setPendingReferences] = useState<string[]>(
    initialData?.references_from?.map(ref => ref.target_note_id) || []
  )

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    const tags = tagsInput
      .split(',')
      .map(tag => tag.trim())
      .filter(tag => tag.length > 0)

    await onSubmit({
      title,
      content,
      note_type: noteType,
      tags,
      references: pendingReferences
    })
  }

  const handleAddReference = (targetNoteId: string) => {
    if (!pendingReferences.includes(targetNoteId)) {
      setPendingReferences([...pendingReferences, targetNoteId])
    }
  }

  const handleRemoveReference = (targetNoteId: string) => {
    setPendingReferences(pendingReferences.filter(id => id !== targetNoteId))
  }

  return (
    <div className="bg-slate-800/90 backdrop-blur-sm rounded-xl shadow-2xl p-8 border border-slate-700">
      <h2 className="text-2xl font-bold mb-8 text-white">
        {initialData ? 'Edit Note' : 'Create New Note'}
      </h2>

      <form onSubmit={handleSubmit} className="space-y-8">
        {/* Title */}
        <div>
          <label className="block text-sm font-medium mb-3 text-slate-300">Title</label>
          <input
            type="text"
            value={title}
            onChange={(e) => setTitle(e.target.value)}
            className="w-full p-4 bg-slate-700/50 border border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent text-white placeholder-slate-400 transition-all duration-200"
            placeholder="Enter note title..."
            required
          />
        </div>

        {/* Note Type */}
        <div>
          <label className="block text-sm font-medium mb-3 text-slate-300">Note Type</label>
          <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
            {NOTE_TYPES.map((type) => (
              <button
                key={type.value}
                type="button"
                onClick={() => setNoteType(type.value)}
                className={`p-4 text-left border rounded-lg transition-all duration-200 ${
                  noteType === type.value
                    ? 'border-blue-500 bg-blue-500/20 text-blue-400'
                    : 'border-slate-600 bg-slate-700/30 text-slate-300 hover:border-slate-500 hover:bg-slate-700/50'
                }`}
              >
                <div className="font-medium text-sm">{type.label}</div>
                <div className="text-xs text-slate-400 mt-1">{type.description}</div>
              </button>
            ))}
          </div>
        </div>

        {/* Content */}
        <div>
          <label className="block text-sm font-medium mb-3 text-slate-300">Content</label>
          <textarea
            value={content}
            onChange={(e) => setContent(e.target.value)}
            rows={12}
            className="w-full p-4 bg-slate-700/50 border border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent text-white placeholder-slate-400 transition-all duration-200 resize-none"
            placeholder="Write your note content..."
          />
        </div>

        {/* Tags */}
        <div>
          <label className="block text-sm font-medium mb-3 text-slate-300">Tags</label>
          <input
            type="text"
            value={tagsInput}
            onChange={(e) => setTagsInput(e.target.value)}
            className="w-full p-4 bg-slate-700/50 border border-slate-600 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent text-white placeholder-slate-400 transition-all duration-200"
            placeholder="Enter tags separated by commas (e.g. dragon, combat, important)"
          />
          <p className="text-xs text-slate-400 mt-2">
            Separate multiple tags with commas
          </p>
        </div>

        {/* References Section */}
        <div>
          <div className="flex justify-between items-center mb-3">
            <label className="text-sm font-medium text-slate-300">References</label>
            <button
              type="button"
              onClick={() => setShowReferenceModal(true)}
              className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg text-sm font-medium transition-colors duration-200 flex items-center space-x-2"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
              </svg>
              <span>Add Reference</span>
            </button>
          </div>

          {pendingReferences.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
              {pendingReferences.map((refId) => {
                // For existing notes, we have the reference data
                const existingRef = initialData?.references_from?.find(ref => ref.target_note_id === refId)
                
                return (
                  <div
                    key={refId}
                    className="bg-slate-700/30 border border-slate-600 rounded-lg p-3 flex items-center justify-between group"
                  >
                    <div className="flex items-center space-x-2">
                      {existingRef ? (
                        <>
                          <span className="text-lg">
                            {NOTE_TYPE_ICONS[existingRef.target_note.note_type]}
                          </span>
                          <div>
                            <p className="text-white font-medium text-sm">{existingRef.target_note.title}</p>
                            <p className="text-slate-400 text-xs capitalize">{existingRef.target_note.note_type}</p>
                          </div>
                        </>
                      ) : (
                        <div>
                          <p className="text-white font-medium text-sm">Referenced Note</p>
                          <p className="text-slate-400 text-xs">Will be linked when saved</p>
                        </div>
                      )}
                    </div>
                    <button
                      type="button"
                      onClick={() => handleRemoveReference(refId)}
                      className="opacity-0 group-hover:opacity-100 p-1 text-slate-400 hover:text-red-400 transition-all duration-200"
                    >
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                      </svg>
                    </button>
                  </div>
                )
              })}
            </div>
          ) : (
            <div className="text-slate-400 text-sm bg-slate-700/20 border border-slate-600 rounded-lg p-4 text-center">
              No references added yet. Click "Add Reference" to link this note to other notes.
            </div>
          )}
        </div>

        {/* Buttons */}
        <div className="flex space-x-4 pt-4">
          <button
            type="submit"
            disabled={isLoading || !title.trim()}
            className="flex-1 bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white p-4 rounded-lg font-medium disabled:opacity-50 disabled:cursor-not-allowed transition-all duration-200 shadow-lg hover:shadow-xl transform hover:scale-105 disabled:transform-none"
          >
            {isLoading ? 'Saving...' : (initialData ? 'Update Note' : 'Create Note')}
          </button>
          <button
            type="button"
            onClick={onCancel}
            className="flex-1 bg-slate-600 hover:bg-slate-500 text-white p-4 rounded-lg font-medium transition-all duration-200"
          >
            Cancel
          </button>
        </div>
      </form>

      {/* Reference Modal */}
      <ReferenceModal
        isOpen={showReferenceModal}
        currentNoteId={initialData?.id || ''}
        existingReferences={pendingReferences}
        onAddReference={handleAddReference}
        onClose={() => setShowReferenceModal(false)}
      />
    </div>
  )
}